<template>
  <v-app>
    <v-main>
      <v-app-bar color="#FF0070">
        <v-btn icon @click="toggleMenu" color="white">
          <v-icon style="color:white">mdi-menu</v-icon>
        </v-btn>
        Chatizard
      </v-app-bar>

      <div v-for="(message, index) in messages" :key="index">
        <v-card :class="message.nome !== 'eumaledita' ? 'teal' : 'red'" outlined class="ma-3 accent-1">
          <v-card-subtitle class="font-weight-bold">{{ message.nome }}</v-card-subtitle>
          <v-card-text>{{ message.text }}</v-card-text>
        </v-card>
      </div>

      <!-- rodapé -->
      <v-app-bar fixed bottom color="#424242">
        <v-text-field v-model="textInput" @keyup.enter="sendMessage()" solo hide-details :label="textInput"></v-text-field>
        <v-btn @click="sendMessage()" color="red">
          <v-icon>mdi-send</v-icon>
        </v-btn>
      </v-app-bar>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data() {
    return {
      textInput: '',
      messages: [
        {
          nome: 'Yuuto Kunihiro',
          text: 'Olá'
        },
        {
          nome: 'Yuuto Kunihiro',
          text: 'tudo bem????'
        }
      ]
    }
  },
  methods: {
    sendMessage() {
      let message = {
        nome: 'eumaledita',
        text: this.textInput
      }
      this.messages.push(message)
      this.textInput = ''
    }
  }
}
</script>